@font-face {
  font-family: "omyu";
  src: url(../public/font/omyu\ pretty.ttf);
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "omyu";
}

a {
  color: inherit;
  text-decoration: none;
}

ul {
  list-style: none;
  display: flex;
}

/* 별 스타일 */
.shooting-star {
  position: absolute;
  width: 4px;
  height: 4px;
  background-color: #f8f3e2;
  transform: rotate(45deg);
  opacity: 1;
  animation: sparkle 1.5s infinite alternate;
  box-shadow: 0 0 15px rgb(255, 251, 218);
  z-index: 5;
}

@keyframes sparkle {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0.5;
  }
}

.cloud-contents {
  position: absolute;
  width: 100%;
  height: 600px;
  top: 0;
  left: 0;
  overflow: hidden;
  /* background-color: #000; */
}

.cloud-contents .cloud {
  width: 220px;
  height: 48px;
  background-color: #fff;
  border-radius: 25% / 100%;
  position: absolute;
  top: 20%;
  left: 0px;
  right: 50px;
  animation: drift 0.5s linear infinite;
  filter: drop-shadow(3px 5px 5px rgba(0, 0, 0, .1));
  z-index: 11;
}

.cloud::before,
.cloud::after {
  content: "";
  position: absolute;
  border-radius: 100%;
  background-color: #fff;
}

.cloud::before{
  width: 100px;
  height: 100px;
  bottom: 0px;
  left: 20px;
}

.cloud::after{
  width: 100px;
  height: 80px;
  right: 20px;
  bottom: 0px;
}

.cloud-contents .cloud_1 {
  left: 0;
  top: 34%;
  animation-duration: 25s;
}

.cloud-contents .cloud_2 {
  left: 60%;
  top: 20%;
  animation-duration: 20s;
  z-index: 10;
}

.cloud-contents .cloud_3 {
  left: 85%;
  top: 50%;
  animation-duration: 25s;
}

.cloud-contents .cloud_4 {
  left: 50px;
  top: 80%;
  animation-duration: 20s;
  z-index: 10;
}

@keyframes drift {
  0% {
    transform: translateX(0);
  }
  50% {
    transform: translateX(40px);
  }
  100% {
    transform: translateX(0);
  }
}

.app-container {
  position: relative;
  padding: 26px 0;
  min-height: 100vh;
}

.app-container .background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}

.day .background {
  background: linear-gradient(to bottom, #5999ec, #8cbbe7, #e8f6ff);
  background-size: cover;
}

.night .background {
  background: linear-gradient(to bottom, #132447, #17284b, #1b2c4f, #1f3154, #233558, #2c4263, #36506d, #415d77, #5b7788, #7a919a, #9caaad, #c1c4c4);
  background-size: cover;
}

.sun {
  position: absolute;
  width: 37vw;
  height: 37vw;
  border-radius: 50%;
  background-image: linear-gradient(to left top, #ffd651, #ffc848, #ffba40, #ffac3b, #fe9e37);
  box-shadow: 0 0 100px #fc8f13;
  z-index: 9;
}

.moon {
  position: absolute;
  width: 37vw;
  height: 37vw;
  border-radius: 50%;
  background-image: linear-gradient(to left top, #f7db65, #f7d056, #f7c448, #fcc04a, #f7ac2d);
  box-shadow: 0 0 100px rgba(241, 196, 15, 0.8);
  z-index: 8;
}

.app-container .sky-object-contents {
  position: absolute;
  top: 10px;
  left: 62%;
  transform: translateX(-62%);
  z-index: 7;
}

.app-container .weather-contents {
  position: relative;
  width: 82%;
  height: auto;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.3);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.50);
  padding: 52px 40px;
  margin: 0 auto;
  text-align: center;
  z-index: 10;
  border-radius: 16px;
  background: none;
}

.day .weather-contents {
  background: rgba(255, 255, 255, 0.3);
  color: #000000;
}

.night .weather-contents {
  background: rgba(71, 68, 68, 0.74);
  color: white;
}

.app-container .weather-contents .forecast-list {
  max-width: 1300px;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  margin: auto;
  margin-top: 80px;
}

.app-container .weather-contents .forecast-list .forecast-item {
  position: relative;
  font-size: 18px;
  border-left: 1px solid #00000070;
  padding: 10px;
}

.night .weather-contents .forecast-list .forecast-item {
  border-left: 1px solid #ffffffab;
}

.app-container .weather-contents .forecast-list .forecast-item:first-child {
  border-left: none;
}


.app-container .weather-contents .forecast-list .forecast-item .temperature {
  padding: 10px;
  font-size: 18px;
}

.app-container .weather-contents .forecast-list .forecast-item .w-icon {
  display: flex;
  gap: 8px;
  justify-content: center;
}

.app-container .weather-contents .forecast-list .forecast-item .w-icon .icon {
  display: flex;
  align-items: center;
  /* font-size: 16px; */
}

.app-container .weather-contents .forecast-list .forecast-item .w-icon .icon .w-text {
  padding-left: 2px;
}


.app-container .weather-contents .data {
  display: flex;
  justify-content: space-between;
  padding: 0 10px;
  padding-bottom: 4px;
  border-bottom: 1px solid #00000070;
}

.night .weather-contents .data {
  border-bottom: 1px solid #ffffffab;
}

.app-container .weather-contents .data .time {
  display: flex;
  font-size: 22px;
  font-weight: 400;
  gap: 12px;
  margin-top: 6px;
}

.night .time>p {
  color: #fff;
}

.app-container .weather-contents .data .but {
  display: flex;
}

.app-container .weather-contents .data .but input {
  width: 0;
  padding: 5px;
  font-size: 22px;
  background-color: transparent;
  border: none;
  outline: none;
  color: #000;
  transition: width 0.5s ease, padding 0.5s ease;
  text-align: end;
}

.night .weather-contents .data .but input {
  color: #fff;
}

.day .weather-contents .data .but input::placeholder {
  color: #000;
}

.night .weather-contents .data .but input::placeholder {
  color: #fff;
}

.app-container .weather-contents .data .but input:focus::placeholder {
  color: transparent;
}

.app-container .weather-contents .data .but input.visible {
  width: 220px;
  padding: 0 32px;
}

.app-container .weather-contents .data .but input.hidden {
  width: 0;
  padding: 0 32px;
}

.app-container .weather-contents .data .but button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  margin-left: -20px;
}

.night .icon {
  color: #fff;
}

.app-container .weather-contents .current-weather {
  margin-top: 40px;
}

.app-container .weather-contents .current-weather .weather-img {
  display: flex;
  justify-content: center;
}

.app-container .weather-contents .current-weather .weather-img .text {
  align-content: center;
  text-align: start;
  padding-left: 32px;
}

.app-container .weather-contents .current-weather .weather-img .text h3 {
  font-size: 6rem;
  font-weight: 100;
}

.app-container .weather-contents .current-weather .weather-img .text-s {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  font-size: 18px;
  line-height: 30px;
  padding-top: 4px;

}

.app-container .weather-contents .current-weather h2 {
  margin-bottom: 60px;
  font-size: 48px;
  font-weight: 100;
}


@media screen and (max-width:1230px) {

  .cloud-contents .cloud_4 {
    left: -10px;
  }

  .app-container .weather-contents .data {
    border: none;
  }

  .night .weather-contents .data {
    border: none;
  }

  .app-container .weather-contents .forecast-list {
    overflow-x: auto;
    white-space: nowrap;
  }

  .app-container .weather-contents .forecast-list .forecast-item {
    margin-bottom: 12px;
  }


  .app-container .weather-contents .forecast-list::-webkit-scrollbar {
    height: 6px;
  }

  .app-container .weather-contents .forecast-list::-webkit-scrollbar-track {
    background: rgba(188, 215, 236, 0.61);
    border-radius: 10px;
  }

  .app-container .weather-contents .forecast-list::-webkit-scrollbar-thumb {
    background-color: #ffd16d;
    border-radius: 10px;
  }

  .app-container .weather-contents .forecast-list::-webkit-scrollbar-thumb:hover {
    background-color: #F3A261;
  }
}


@media screen and (max-width:900px) {

  /* .app-container {
    min-height: 146vh;
  } */

  .cloud-contents .cloud{
    width: 180px;
    height: 34px;
  }

  .cloud-contents .cloud_1{
    left: -5%;
  }

  .cloud-contents .cloud_3{
    top: 84%
  }

  .cloud::before{
    width: 80px;
    height: 80px;
  }

  .cloud::after{
    width: 60px;
    height: 60px;
    right: 30px;
  }

  .app-container .sky-object-contents {
    left: 61%;
    transform: translateX(-61%);
  }

  .app-container .weather-contents {
    width: 90%;
    padding: 32px 40px;
  }

  .app-container .weather-contents .current-weather {
    margin-top: 60px;
  }

  .app-container .weather-contents .forecast-list {
    grid-template-columns: 1fr;
    border-top: 1px solid #00000070
  }

  .night .weather-contents .forecast-list {
    border-top: 1px solid #ffffffab;
  }

  .app-container .weather-contents .forecast-list .forecast-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: none;
  }

  .app-container .weather-contents .forecast-list .forecast-item .temperature {
    display: flex;
    align-items: center;
    font-size: 20px;
  }

  .app-container .weather-contents .forecast-list .forecast-item .w-icon {
    gap: 18px;
  }

  .app-container .weather-contents .forecast-list .forecast-item .w-icon .icon {
    font-size: 20px;
  }

  .app-container .weather-contents .forecast-list .forecast-item .description {
    display: none;
  }

  .app-container .weather-contents .data .time .current {
    display: none;
  }
}


@media screen and (max-width:610px) {

  .app-container .sky-object-contents{
    left: 0;
  }

  .sun{
    width: 260px;
    height: 260px;
  }

  .cloud-contents .cloud{
    width: 140px;
    height: 26px;
  }

  .cloud::before{
    width: 68px;
    height: 68px;
    left: 18px;
  }

  .cloud::after{
    width: 42px;
    height: 42px;
    right: 20px;
    bottom: 8px;
  }
  
  .cloud-contents .cloud_2{
    top: 26%;
    left: 70%;
  }

  .cloud-contents .cloud_3{
    top: 70%;
    left: 80%;
  }

  .cloud-contents .cloud_4 {
    display: none;
  }

  .app-container .weather-contents {
    padding: 18px;
  }

  .app-container .weather-contents .data {
    flex-direction: column;
    gap: 18px;
  }

  .app-container .weather-contents .forecast-list {
    margin-top: 28px;
  }

  .app-container .weather-contents .forecast-list .forecast-item {
    padding: 22px 10px;
    margin-bottom: 0;
  }

  .app-container .weather-contents .data .time {
    order: 2;
    justify-content: center;
    font-size: 18px;
  }

  .app-container .weather-contents .data .but {
    order: 1;
    justify-content: flex-end;
  }

  .app-container .weather-contents .current-weather {
    margin-top: 12px;
  }

  .app-container .weather-contents .current-weather h2 {
    font-size: 36px;
    margin-bottom: 30px;
  }

  .app-container .weather-contents .current-weather .weather-img {
    display: flex;
    flex-direction: column;
    line-height: 90px;
    align-items: center;
  }

  .app-container .weather-contents .current-weather .weather-img>img {
    max-width: 220px;
  }

  .app-container .weather-contents .current-weather .weather-img .text {
    text-align: center;
    padding-left: 0;
  }

  .app-container .weather-contents .current-weather .weather-img .text h3 {
    font-size: 5.5rem;
  }

  .app-container .weather-contents .current-weather .weather-img .text-s {
    display: flex;
    justify-content: center;
    gap: 20px;
    margin-top: 30px;
  }

}

@media screen and (max-width:500px) {

  .sun, .moon{
    width: 200px;
    height: 200px;
  }

  .cloud-contents .cloud_1 {
    left: -50px;
    top: 34%;
    z-index: 10;
  }

  .cloud-contents .cloud_3{
    left: 70%;
    z-index: 10;
  }



  .app-container .weather-contents .forecast-list .forecast-item .w-icon .wind {
    display: none;
  }

  .app-container .weather-contents .forecast-list .forecast-item>img {
    width: 30%;
  }

  .app-container .weather-contents .forecast-list .forecast-item {
    padding: 10px 0;
  }

  .app-container .weather-contents .forecast-list .forecast-item .temperature {
    font-size: 16px;
  }

  .app-container {
    padding-top: 40px;
  }
}